@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,500;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    box-sizing: border-box;
}
body{
    background-color: #f2f0f0;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
a{
    text-decoration: none;
    color: black;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.logo{
    display: block;

}
.header{
    display: flex;
    background-color: #c7660c;
    height: 50px;
    font-family: 'Roboto';
    font-size: 25px;
    align-items: center;
    padding: 0 25px;
    border-bottom: 1px solid darkslategray;

    width: 100%;
}
.active{
    color: orange;
}
.header .logo{
    margin-right: 25px;
}
.header a:hover{
    color: #fff;
    transition: color .3s;
}

.header a {
    margin-left: 10px;
}
.Body{
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.Navbar {
    padding: 50px 35px 0 35px;
    
}
.Navbar::after{
    display: block;
    content: '';
    width: 75px;
    height: 1px;
    background-color: black;
    margin: 0 auto;
    margin-top: 10px;
}

.BtnNavBar{
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Roboto';
    font-weight: 500;
    white-space: nowrap;
    padding: 0 10px;
    
    border-radius: 3px;
}

.BtnNavBar:hover{
    background-color: #e09e5f;
    
   
    transition: background-color .3s;
    cursor: pointer;  
}
.Content{
    margin: 0 15px;
    padding: 50px 10% 0 10%;
}
NavLink{
    color: black;
}
.logout{
  margin-left: auto;
  cursor: pointer;
}
.ProfileHeader {
    width: 100%;
    font-family: 'Roboto Mono';
    display: flex;
    font-weight: 300;
    font-size: 40px;
    background-color: #d4d4d4;
    height: 110px;
    padding: 0 25px;
    border-radius: 3px;
    margin-bottom: 30px;
}
.ProfilePhoto{
    margin-right: 50px;
    border: 2px solid #694320;
    border-radius: 3px;
    align-self: center;
}
.FullName{
    display: flex;
    align-items: flex-end;
}
.AllPosts{
    background-color: #d4d4d4;
    border-radius: 3px;
    font-size: 30px;
    padding: 10px 50px;
}
.SimplePost img {
    border-radius: 50% 0 0 50%;
    align-self:  flex-start;
}
.SimplePost{
    display: flex;
    font-family: 'Roboto Mono';
    margin-top: 25px;
    padding: 0 15px;
}
.PostText{
    background-color: white;
    border-radius: 0 4px 4px 4px;
    font-size: 20px;
    width: 100%;
    padding: 15px;
}
.NewPost{
    width: 100%;
    background-color: #d4d4d4;
    margin-bottom: 30px;
    min-height: 75px;
    border-radius: 3px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.NewPost textarea{
    width: 90%;
    max-width: 100%;
    min-height: 60px;
    resize: none;
}
.NewPost textarea:focus{
    outline: none;
}
.NewPost button:focus{
    outline: none;
}

.NewPost button{
    align-self: center;
    height: 45px;
    width: 20%;
    border: 0;
    border-radius: 3px;
}
.NewPost button:hover{
    background-color: darkseagreen;
    transition: background-color .3s;
    cursor: pointer;
}
.NewPost button:active{
    background-color: dodgerblue;
}
.aboutBlock{
    min-height: 150px;
    background-color: #d4d4d4;
    margin-bottom: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    font-size: 20px;
}

.DialogWindow{
    display: flex;
}
.AllFriendsDialog{
    width: 20%;
    font-family: 'Roboto Mono';
    font-weight: 0;
    background-color: #d4d4d4;
    padding: 10px 15px 20px 15px;
    border-radius: 3px;
    align-self: flex-start;

}

.DialogWithUser{
    width: 80%;
    font-family: 'Open Sans';
    background-color: #d4d4d4;
    border-radius: 3px;
    margin-left: 15px;
    height: 80vh;
    padding: 4%;

}
.SimleFriendDialog{
    min-height: 50px;
    font-size: 25px;
    display: flex;
    align-items: flex-end;
}
.SimleFriendDialog:hover{
    background-color: #c28051;
    transition: background-color .15s;
    cursor: pointer;
    border-radius: 3px;
}
.SimleFriendDialog.active{
    color: inherit;
    background-color: #fff;
    border-radius: 3px;
}
::-webkit-scrollbar{
    width: 0px;
    background: transparent;
}
.Messages{
    width: 100%;
    height: 90%;
    margin-bottom: 15px;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    border-bottom:1px solid grey;
}
.SendMessage{
    height: 10%;
}
form{
    height: 100%;
}
textarea{
    width: 100%;
    height: 100%;
    resize: none;
    border: 0;
    border-radius: 2px;
    outline: none;
    font-size: 15px;
}

.SimpleMessage {
    background-color: #fff;
    margin-bottom: 10px;
    font-size: 22px;
    padding: 2px 10px;
    border-radius: 3px;

}

.me{
    text-align: right;
    margin-left: auto;
    max-width: 85%;
}
.MessageFromFriend{
    margin-right: auto;
    max-width: 85%;
}



.FindUserWindow{
    width: 100%;
    height: 100%;
}
.FindUsersInput
{
    height: 100%;
    width: 85%;
    text-align: center;
    outline: none;
    font-size: 20px;
    border: 0;
    border-radius: 3px;
}
.FindUsersInputWindow{
    height: 40px;
}
.FindUsersInputWindow button{
    width: 15%;
    height: 100%;
    margin: 0;
    font-size: 20px;
}
.TypicalFriend{
    
    height: 50px;
    
    border-radius: 3px;
    width: 80%;
    margin: 0 auto;
    margin-top: 10px;
    display: flex;
}
.SearchFriendBlock {
    width: 90%;
    display: flex;
    justify-content: space-between;
}
.followButton{
    min-width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin-right: 25px;
}
.blabla{
    width: 90%;
    margin-left: 5%;
    background-color: #f0a45d;
    border-radius: 3px;
    padding: 3px 15px;
}
.redbgc{
    background-color: #f24b4b;
}
.greenbgc{
    background-color: #5ee53c;
}
.MoreButton{
    background-color: #3c93e5;
    border: 0;
    outline: none;
    width: 7%;
    height: 35px;
    border-radius: 3px;
    cursor: pointer;
}
.MoreButtonDiv{
    display: flex;
    justify-content: center;
    margin-top: 15px;
    
}
.MoreButton:active{
    background-color: #3ce580;
    transition: background-color .2s;
}
.circle{
    border-radius: 55%;
}
.rotating{
    -webkit-animation-name: rotate;
            animation-name: rotate;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
  }
  
  @-webkit-keyframes rotate {
    from { 
      transform: rotate(0deg); 
    }
    to {
      transform: rotate(-360deg);
    }
  }
  
  @keyframes rotate {
    from { 
      transform: rotate(0deg); 
    }
    to {
      transform: rotate(-360deg);
    }
  }
  .FindUsersresultate{
      display: flex;
      flex-direction: column;
      
      align-items: center;
  }
  .loader{
      margin-top: 50px;
  }
  .lds-ring {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid #fff;
    border-radius: 50%;
    -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #fff transparent transparent transparent;
  }
  .lds-ring div:nth-child(1) {
    -webkit-animation-delay: -0.45s;
            animation-delay: -0.45s;
  }
  .lds-ring div:nth-child(2) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
  }
  .lds-ring div:nth-child(3) {
    -webkit-animation-delay: -0.15s;
            animation-delay: -0.15s;
  }
  @-webkit-keyframes lds-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes lds-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
